import React from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';

import { Button } from 'antd';
import { WeaSwitch } from 'comsMobx';
import { WeaTop, WeaTab, WeaRightMenu, WeaReport, WeaFormItem, WeaNewScroll } from 'ecCom';

import { renderNoright, renderLoading } from '../util'; // formutil

@inject('reportStore')
@observer
export default class Report extends React.Component {
  static defaultProps = {
    prefixCls: "wea-demo08"
  };

  componentWillMount() { // 
    const { reportStore: { doInit } } = this.props;
    doInit();
  }

  componentWillReceiveProps(nextProps) {
    const { reportStore } = this.props;
    if (this.props.location.key !== nextProps.location.key) { //  
      reportStore.doInit();
    }
  }

  // excel: 
  exportExcel = () => {
    console.log('excel');
  }

  // 
  doSearch = () => {
    const { reportStore: { getDatas, form } } = this.props;
    const params = form.getFormParams();
    getDatas(params);
  }

  // 
  getSearchsAdQuick = () => {
    const { reportStore: { form, condition } } = this.props;
    const { isFormInit } = form;
    const arr = [];
    if (isFormInit) {
      toJS(condition)[0].items.map(field => {
        arr.push(
          <WeaFormItem
            label={`${field.label}`}
            labelCol={{span: `${field.labelcol}`}}
            wrapperCol={{span: `${field.fieldcol}`}}
          >
            <WeaSwitch fieldConfig={field} form={form} />
          </WeaFormItem>
        );
      });
    }
    return arr;
  }
  
  render() {
    const { reportStore, prefixCls } = this.props;
    const { loading, hasRight, form, datas } = reportStore;

    if (!hasRight && !loading) { // 
      return renderNoright();
    }

    const rightMenu = [// 
      {
        key: 'BTN_EXPORT',
        icon: <i className='icon-coms-export'/>,
        content : 'Excel',
        onClick : this.exportExcel,
      },
    ];
    const collectParams = { // 
      favname: '',
      favouritetype: 1,
      objid: 0,
      link: 'wui/index.html#/ns_demo08/index',
      importantlevel: 1,
    };
    const adBtn = [ // 
      <Button type="primary" onClick={this.doSearch}></Button>,
      <Button type="ghost" onClick={() => form.resetForm()}></Button>,
      <Button type="ghost" onClick={() => setShowSearchAd(false)}></Button>,
    ];
    const btns = [ // 
      <Button type="primary" onClick={this.exportExcel}>Excel</Button>,
    ]

    return (
      <div className={prefixCls}>
        <WeaRightMenu
          datas={rightMenu} // 
          collectParams={collectParams} // :   3
        >
          <WeaTop
            title="" // 
            icon={<i className='icon-coms-meeting' />} // 
            iconBgcolor='#F14A2D' // 
            buttons={btns}
            showDropIcon={true} // 
            dropMenuDatas={rightMenu} // 
            dropMenuProps={{ collectParams }} // :   3
          >
            <WeaNewScroll height={'100%'}>
              <WeaTab
                searchsAdQuick={this.getSearchsAdQuick()} // 
                buttonsAdQuick={[<Button type="primary" onClick={this.doSearch}></Button>]} // 
              />
              {loading ? renderLoading() : (
                <div className={`${prefixCls}-datas`}>
                  <WeaReport
                    datas={toJS(datas)}
                  />
                </div>
              )}
            </WeaNewScroll>
          </WeaTop>
        </WeaRightMenu>
      </div>
    )
  }
}


