import { observable, action, toJS } from 'mobx';
import { message } from 'antd';
import { WeaForm } from 'comsMobx';

import * as API from '../apis'; // API

export class ReportStore {
  @observable form =  new WeaForm(); // form
  @observable loading = true; // loading
  @observable hasRight = true; //  
  @observable condition = []; // 
  @observable datas = []; // 

  // 
  @action
  doInit = () => {
    this.getCondition();
    this.getDatas({year: '2019'});
  }

  // 
  @action
  getCondition = () => {
    API.getCondition().then(action(res => {
      if (res.api_status) { // /
        this.condition = res.condition;
        this.form.initFormFields(res.condition); // form
      } else {
        message.error(res.msg || '')
      }
    }));
  }

  // 
  @action
  getDatas = (params) => {
    this.loading = true;
    API.getDatas(params).then(action(res => {
      if (res.api_status) { // /
        this.datas = res.data;
        this.hasRight = res.hasRight;
      } else {
        message.error(res.msg || '')
      }
      this.loading = false;
    }));
  }

}


